@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.post-card {
  height: 200px !important;
  width: 700px;
  display: flex;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: rgb(37, 47, 67) 0px 8px 30px -10px;
  transition: transform ease 300ms;
  margin-bottom: 40px;
}

.post-card:hover {
  transform: scale(1.02, 1.02);
}

.post-card-photo {
  height: 100%;
  max-width: 200px;
}

.post-card-text {
  padding: 12px;
}

.post-card-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: #282c34;
  font-family: "Oswald", sans-serif;
}

.post-date {
  font-size: 0.94rem;
  margin-top: 6px;
  font-weight: 100;
  color: rgba(0,0,0,0.65);
}

.post-card-summary {
  font-size: 1.1rem;
  margin-top: 6px;
  color: rgba(0,0,0,0.7);
  overflow: hidden;
  height: 7.2em; /* 6 lines */
  position: relative;
}

.post-card-summary:after {
  content: "";
  text-align: right;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 70%;
  line-height: 1.2em;
  height: 1.2em;
  background: linear-gradient(to right, rgba(255,255,255,0), rgba(255,255,255,1) 70%);
}

.post-card-open {
  height: max(100vh, 100%) !important;
  width: 100vw;
  display: flex;
  transition: transform ease 400ms;
  z-index: 999;
  background-color: white;
}

.post-card-text-open {
  display: flex;
  width: 100vw;
  align-items: center;
  flex-direction: column;
  padding: 12px;
}

.post-card-title-open {
  font-size: 3rem;
  font-weight: 700;
  color: #282c34;
  font-family: "Oswald", sans-serif;
}

.exit-container {
  display: flex;
  justify-content: center;
  color: #ec5200;
  transition: background-color ease 500ms;
  border-radius: 8px;
}

.exit-text {
  margin: 14px 0 0 0;
  padding-right: 12px;
}

.exit-container:hover {
  background-color: rgba(236,82,0,0.3);
}

.exit-button {
  height: 24px;
  padding: 12px;
}

@media only screen and (max-width: 420px) {
  .post-card { 
    margin-bottom: 30px; 
    height: 120px !important;
    width: 90vw;
  }
  .post-card-photo {
    max-width: 120px;
  }
  .post-card-title {
    font-size: 1rem;
  }
  .post-date {
    font-size: 0.8rem;
  }
  .post-card-summary {
    font-size: 0.8rem;
    line-height: 1.2em;
    height: 3.6em; /* 3 lines */
  }
}
.not-home {
  margin-top: 110px;
}

.booklist-container {
  max-width: 700px;
  margin: 20px 40px 40px 40px;
  color: #282c34;
  font-family: "Oswald";
  height: 100%;
}

p a {
  text-decoration: underline;
}

table {
  width: 100%;
  border-collapse: collapse;
}

li span {
  font-weight: 700;
}

ul {
  font-weight: 300;
  font-size: 1.1rem;
  font-family: "Oswald", sans-serif;
}

strong {
  font-weight: 700;
}

.table-header {
  font-size: 1.5rem;
  font-weight: 700;
  border-bottom: 2px solid rgba(36,108,255);
}

table tr {
  font-size: 1.1rem;
  font-weight: 300;
  font-family: "Oswald", sans-serif;
}

table tr td {
  padding: 6px;
}

table tr:nth-child(even) {
  background-color: rgba(0,108,255,0.15);
  color: black;
}
.contact-card {
  height: 550px;
  width: 400px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 6px;
  box-shadow: rgb(37, 47, 67) 0px 8px 30px -10px;
  margin-top: 140px;
  transition: all ease 300ms;
}

.contact-card:hover {
  transform: scale(1.02, 1.02);
  box-shadow: rgb(37, 47, 67) 0px 8px 30px -12px;
}

.card-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.github, .insta, .linkedin {
  background-color: black;
  width: 70%;
  height: 44px;
  margin: 12px auto;
  display: flex;
  padding-left: 40px;
  align-items: center;
  border-radius: 6px;
  transition: background-color ease 400ms;
}

.github:hover, .insta:hover, .linkedin:hover {
  background-color: #ec5200;
}

.github > a,
.github > a:hover,
.insta > a, 
.insta > a:hover, 
.linkedin > a,
.linkedin > a:hover {
  color: white;
}

.fa-icon {
  color: white;
  font-size: 20px;
  padding-right: 10px;
}

.profile {
  width: 100%;
}

@media only screen and (max-width: 425px) {
  .contact-card {
    height: 525px;
    width: 350px;
    margin-top: 140px;
  }
}

@media only screen and (max-width: 375px) {
  .contact-card {
    height: 488px;
    width: 325px;
    margin-top: 130px;
  }
}

@media only screen and (max-width: 320px) {
  .contact-card {
    height: 420px;
    width: 280px;
    margin-top: 110px;
  }
  .insta, .linkedin {
    height: 40px;
  }
  .insta > a, .linkedin >a {
    color: white;
    font-size: 0.9rem !important;
  }
}
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.about {
  max-width: 700px;
  margin: 20px 40px 40px 40px;
  font-family: "Oswald", sans-serif;
}

p {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.1rem;
  font-weight: 300;
}

hr {
  height: 1px;
  border: none;
  background-color: rgba(0,0,0,0.2)
}

@media only screen and (max-width: 375px) {
  p {
    font-size: 0.95rem;
  }
}
a {
  color: #282c34;
  cursor: pointer;
  font-weight: 300;
  text-decoration: none;
  transition: color ease 400ms;
  padding: 6px 0;
}

a:hover {
  color: #ec5200;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  background-color: white;
  position: fixed;
  top: 0;
  box-shadow: rgb(37, 47, 67) 0px 10px 10px -12px;
  z-index: 9999;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 40px;
  height: 80px;
  width: 700px;
}

.brand {
  color: #282c34;
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
  font-weight: 900;
  text-decoration: none;
  text-transform: uppercase;
}

.brand:hover {
  color: #282c34;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

